body {
	margin: 0;
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	::selection {
		background-color: #bdd631;
	}
}

.flex {
	display: flex;
	justify-content: center;
	align-items: flex-start;
}

h1 {
	font-size: 2.5rem;
}
h2 {
	font-size: 1.5rem;
}

h1,
h2,
h3,
p {
	opacity: 0;
	cursor: default;
	letter-spacing: .1rem;
}

button {
	opacity: 0;
}

.nav button {
	opacity: 1;
}
